<template>
  <!-- 产品管理 -->
  <div class="main_con_box">
    <div class="top_box">
      <div>
        <el-input
          style="width: 200px"
          size="mini"
          v-model="keyword"
          placeholder="编码/缩写/名称"
        ></el-input>
        <el-select
          class="top_sel"
          size="mini"
          v-model="product_classification"
          placeholder="产品分类"
        >
          <el-option
            v-for="item in product_classificationlist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>
        <!-- <el-select
          class="top_sel"
          size="mini"
          v-model="manufacturerId"
          placeholder="生产厂家"
        >
          <el-option
            v-for="item in manufacturerlist"
            :key="item.value2"
            :label="item.label"
            :value="item.value2"
          >
          </el-option>
        </el-select> -->
        <el-button
          class="search_btn"
          type="primary"
          size="mini"
          @click="search()"
          >查 询</el-button
        >
        <el-button size="mini" @click="resetForm()">重 置</el-button>
      </div>

      <div>
        <el-button type="primary" size="mini" @click="onSubmit()"
          >新 建</el-button
        >
      </div>
    </div>

    <div class="main_box">
      <el-table
        :data="tableData"
        :span-method="objectspanmethod"
        border
        style="width: 100%"
        :height="$store.state.innerheight - 185"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column
          prop="product_classification"
          label="产品分类"
          width=""
        >
        </el-table-column>
        <el-table-column prop="product_name" label="产品名称" width="">
        </el-table-column>
        <el-table-column prop="product_code" label="产品编码" width="">
        </el-table-column>
        <el-table-column prop="product_abbreviation" label="产品缩写" width="">
        </el-table-column>

        <el-table-column prop="product_specification" label="产品规格" width="">
        </el-table-column>
        <el-table-column prop="unit" label="基本单位" width="">
        </el-table-column>
        <el-table-column prop="standar_unit_price" label="标准单价" width="">
        </el-table-column>
        <el-table-column label="操作" width="">
          <template #default="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="mini"
              >编 辑</el-button
            >
            <el-button
              type="text"
              style="color: red"
              size="mini"
              @click="handleDel(scope.row)"
              >删 除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="footer_box" style="margin-top: 10px">
      <el-pagination
        v-model:currentPage="currentPage4"
        :page-sizes="[20, 50, 70, 100]"
        :page-size="20"
        layout="total, sizes, prev, pager, next, jumper"
        :total="400"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  components: {},
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      currentPage4: 1,
      keyword: "",
      product_classificationId: "",
      product_classificationlist: [],
      manufacturerId: "",
      manufacturerlist: [],
      tableData: [
        {
          product_classification: "质控品A类",
          product_code: "PT01",
          product_abbreviation: "152",
          product_name: "蛋白",
          product_specification: "1:10",
          unit: "盒",
          standar_unit_price: "200",
        },
        {
          product_classification: "质控品A类",
          product_code: "PT02",
          product_abbreviation: "153",
          product_name: "蛋白",
          product_specification: "2:8",
          unit: "盒",
          standar_unit_price: "200",
        },
        {
          product_classification: "质控品A类",
          product_code: "PT03",
          product_abbreviation: "154",
          product_name: "蛋白",
          product_specification: "5:5",
          unit: "盒",
          standar_unit_price: "200",
        },
      ],
    });
    const onSubmit = () => {
      proxy.$router.push("/productInformation");
    };

    const handleEdit = () => {
      proxy.$router.push("/productInformation");
    };
    const handleDel = () => {
      console.log();
    };
    const search = () => {
      console.log();
    };
    const resetForm = () => {
      console.log();
    };
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    // 合并表格的根据某个id值
    const objectspanmethod = ({row,column,rowIndex,columnIndex}) =>{
      console.log(row)
      console.log(column)
      for(var i = 0; i < 2; i++){
        if(i==columnIndex) {
          const _row = proxy.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan:_row,
            colspan:_col
          }
        }
      }
    }
    // 先把获取到的数据按规定值相同的放一起
    const rowspan = () => {
      proxy.spanArr = []
      proxy.position = 0
      proxy.tableData.forEach((item,index) => {
        if(0 == index) {
          proxy.spanArr.push(1)
          proxy.position = 0
        }else{
          if(proxy.tableData[index].code == proxy.tableData[index - 1].code){
            proxy.spanArr[proxy.position] += 1
            proxy.spanArr.push(0)
          }else{
            proxy.spanArr.push(1)
            proxy.position = index
          }
        }
      })
      console.log(proxy.spanArr)
    }
    onMounted(()=>{
      proxy.rowspan()
    })
    return {
      ...toRefs(state),
      onSubmit,
      handleDel,
      search,
      resetForm,
      handleEdit,
      handleSizeChange,
      handleCurrentChange,
      objectspanmethod,
      rowspan,
    };
  },
};
</script>

<style scoped>
.main_con_box {
  padding: 10px;
  box-sizing: border-box;
  background-color: #fff;
}
.top_box {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
}
.top_sel {
  margin-left: 10px;
}
.search_btn {
  margin-left: 10px;
}

.main_box {
  margin-top: 10px;
}
</style>
